.main {
  background-image: url("../assets/images/Main.jpg");
  background-color: rgba(51, 51, 51, 0.9);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: overlay;
  padding-bottom: 120px;

  .main_title {
    padding-top: 120px;

    h1 {
      font-size: 98px;
    }
  }

  .main_btn {
    margin-top: 45px;
  }

  button {
    border: none;
    text-transform: uppercase;
    color: white;
    background-color: #56b26f;
    border-radius: 30px;
    padding: 18px 55px;
    cursor: pointer;

    letter-spacing: 2px;
    font-family: "Inter", sans-serif;
    font-weight: 800;
    transition: color 1s ease;

    &:hover {
      color: #232323;
      transition: color 1s ease;
    }
  }
}

@media (max-width: 1200px) {
  .main {
    background-size: cover;
    text-align: center;
    padding-bottom: 0;

    .main_btn {
      margin-top: 0;
      margin: 45px 0;
    }

    .main_title {
      padding-top: 20%;

      h1 {
        width: 100%;
      }
    }
  }
}

@media (max-width: 1000px) {
  .main {
    .main_title {
      h1 {
        font-size: 68px;
      }
    }
  }
}

@media (max-width: 700px) {
  .main {
    .main_title {
      h1 {
        font-size: 52px;
      }
    }
  }
}

@media (max-width: 700px) {
  .main {
    .main_title {
      padding-top: 24%;
      h1 {
        font-size: 52px;
      }
    }
  }
}


@media (max-width: 500px) {
  .main {
    .main_title {
