    }

    .main_btn {
      margin-top: 0;
      margin: 25px 0;
    }
  }
}

@media (max-width: 400px) {
  .main {
    .main_title {
      padding-top: 38%;
      h1 {
        font-size: 32px;
      }
    }
  }
}